@import 'src/variables.scss';

.passengers-list-form {
  display: flex;
  flex-direction: column;
  align-items: center;

  .button {
    margin: 30px 0;
  }
}

.expandable-panel {
  width: 55%;
  margin: 0 auto 10px;
  border: 1px solid $borderColor;
  box-shadow: $boxShadow;
  border-bottom: 1px solid $passengersListBorderColor;
}

@media screen and (max-width: $tabletSize) {
  .expandable-panel{
    width: 85%;
  }
}

@media screen and (max-width: $mobileSize) {
  .expandable-panel{
    width: 85%;
  }
}
