@import 'src/variables';

.luggage-list {
  position: relative;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-evenly;
  min-height: 70px;
  background-color: $luggageListBackgroundColor;
  padding: 15px 0;

  &__item {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 15px 0;
    cursor: pointer;

    &:not(:last-child) {
      border-bottom: 1px solid $borderColor;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 80px;
    text-align: center;
    line-height: 1.75;
    font-size: 1rem;
    font-weight: 400;
    font-family: $fontFamily;
  }
 
  &__checkbox {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: $luggageCheckboxBackgroundColor;
    width: 120px;
    min-height: 120px;
    margin: 10px 0;
    padding: 10px 0;
    border-radius: 5px;
  }

  &__label {
    text-align: center;
  }

  &__luggage-icon {
    padding: 10px 15px;
    font-size: 2em;
    color: $secondaryTextColor;
  }

  &__kg {
    font-weight: 500;
    font-size: 1em;
    text-transform: uppercase;
  }

  &__price {
    margin-top: 5px;
    font-size: 0.8em;
    color: $secondaryTextColor;
  }

