  input[type="radio"] {
    position: absolute;
    top: 0;
    width: 0;
    height: 0;

    &::after {
      content: "";
      position: absolute;
      top: -3px;
      left: -65px;
      width: 120px;
      height: 140px;
      cursor: pointer;
      border-radius: 5px;
    }
  }

  input[type="radio"]:checked {
    &::after {
      background-color: $selctedLuggageCheckboxBackgroundColor;
    }
  }
